
.colto{columns:2px 2;list-style-position:inside
}
#content{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;padding:30px
}
#personal{margin-top:15px;text-align:right;padding-top:15px
}
#colorpicker{position:fixed;left:40px;top:5px
}
#slider{position:fixed;top:50px;left:10px
}
.exchange{position:fixed;left:10px;top:10px
}
.erase{position:fixed;top:10px;left:80px
}
#left{padding:30px;color:#eee;min-height:100%
}
#left .ui.divider{border-color:#EEEEEE;border-width:1px;border-bottom-width:0;opacity:0.25
}
#left .ui.divider:first-child{display:none
}
#left h1{margin-top:0;text-align:left
}
#left i.circular.icon{background:#eee
}
#left a.centered{position:relative;color:#fff !important;padding-bottom:0;display:block;width:100%;margin-bottom:30px
}
#left a.centered>*{position:relative;z-index:10
}
#left a.centered .before{position:absolute;box-sizing:content-box;width:100%;height:100%;padding:30px;left:-30px;top:-30px
}
.right{-ms-flex-direction:row-reverse;flex-direction:row-reverse
}
#right{width:650px;padding:30px;padding-top:5px;background-color:whitesmoke;min-height:100%
}
.centered{-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;text-align:center
}
.bold{font-weight:bold
}
.italic{font-style:italic
}
.info{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;padding-top:3px;padding-bottom:3px
}
.inverted{filter:invert(100%)
}
h2{margin-top:10px !important
}
@media all{
.page-break{display:none
}
}
@media only screen and (max-width: 420px){
#app>*:not(#content){display:none
}
#app{width:100%
}
#content{padding:0;-ms-flex-direction:column;flex-direction:column
}
#content #left,#content #right{width:100%
}
.colto{columns:auto 1
}
}
@media print{
#content{padding:0
}
.page-break{display:block;page-break-before:always;position:relative
}
#right{background:transparent
}
#right,#left{position:relative
}
#app>*:not(#content){display:none
}
#links{padding-top:5px
}
#left{width:50%
}
.colto{font-size:0.9em
}
}



